import Finisher from "../../components/finisher"
import Topbar from "../../components/topbar"
import './style.css';
import videos from "../../db/videos";
import VideoListItem from "../../components/video/list";
import { setDescription, setKeywords, setTitle } from "../../lib/seo";



export default (props) => {
    setTitle('Videos | Ninja\'s in Pyjamas');
    setDescription('Browse our videos');
    setKeywords('Videos, Ninjas in Pyjamas');
    return (
        <div className="article-root" style={{ minHeight: '250vh' }}>
            <Finisher skew={0} style={{ height: '250px' }}>
                <h1 className="page-title">Videos</h1>
            </Finisher>
            <Topbar />
            <div className="content videos">
                <div className="centre">
                    <div style={{ height: '100px' }}></div>
                    {videos.map((video , idx) => {
                        return <VideoListItem video={video} videoId={idx}/>
                    })}
                </div>
            </div>
        </div>
    )
}