import { useState } from "react"
import './style.css';
import logo from './../../media/logo.png';
import { Link } from "react-router-dom";
import { ROOT } from "../../env";

export default (props) => {

    const [yPos , setYPos] = useState(0);

    window.onscroll = (ev) => {
        setYPos(window.scrollY);
    }

    return (
        <div className={"topbar " + (yPos > 100 ? "scrolled" : "")}>
            <div className="centre">
                <div className="links-left">
                    <Link to={ROOT + ""}>Home</Link>
                    <Link to={ROOT + "blog"}>Blog</Link>
                    <Link to={ROOT + "videos"}>Videos</Link>
                    <Link to={ROOT + "live"}>Live</Link>
                    <Link to={ROOT + "sponsors"}>Sponsors</Link>
                </div>
                <div className="logo">
                    <Link to={ROOT}><img src={logo}/></Link>
                </div>
                <div className="links-right">
                    <Link to="#"><i className="fab fa-facebook"></i></Link>
                    <Link to="#"><i className="fab fa-instagram"></i></Link>
                    <Link to="#"><i className="fab fa-youtube"></i></Link>
                    <Link to="#"><i className="fab fa-snapchat"></i></Link>
                    <Link to="#"><i className="fab fa-twitch"></i></Link>
                    <Link to="#"><i className="fab fa-pinterest"></i></Link>
                </div>
            </div>
        </div>
    )
}