import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import { BrowserRouter , Routes , Route } from 'react-router-dom';

import Homepage from './pages/homepage';
import BlogRoot from './pages/articleroot';
import BlogPost from './pages/article';
import Videos from './pages/videos';
import Theatre from './pages/theatre';
import Sponsorships from './pages/sponsorships';
import Live from './pages/live';

function extractIdFromUrl() {
  const parts = window.location.pathname.split('/');
  const id = parts[3];

  return id;
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <Routes>
        <Route path="/">
            <Route path="insites-demo-build">
              <Route index element={<Homepage />}/>
              <Route path="blog" element={<BlogRoot />}/>
              <Route path="article">
                <Route path=":id" element={<BlogPost />}/>
              </Route>
              <Route path="videos" element={<Videos />}/>
              <Route path="video">
                  <Route path=":id" element={<Theatre videoId={extractIdFromUrl()} />}/>
              </Route>
              <Route path="sponsors" element={<Sponsorships />}/>
              <Route path="live" element={<Live />}/>
            </Route>
        </Route>
      </Routes>
    </BrowserRouter>
  </React.StrictMode>
);

