import Finisher from "../../components/finisher"
import Topbar from "../../components/topbar"
import './style.css';
import articles from "../../db/articles";
import { Link } from "react-router-dom";
import { setDescription, setKeywords, setTitle } from "../../lib/seo";
import { ROOT } from "../../env";

function extractIdFromUrl() {
    const parts = window.location.pathname.split('/');
    const id = parts[3];

    return id;
}

export default (props) => {
    const article = articles[extractIdFromUrl()];
    setTitle(article.meta.title);
    setKeywords(article.meta.keywords.join(' '));
    setDescription(article.meta.description);
    
    return (
        <div className="article-root" style={{ minHeight: '250vh' }}>
            <Finisher skew={0} style={{ height: '250px' }}>
                <h1 className="page-title">Article</h1>
            </Finisher>
            <Topbar />
            <div className="content article-page">
                <div className="centre">
                    <div className="breadcrumbs">
                        <Link to={ROOT}>Home</Link> |
                        <Link to={ROOT + "blog"}>Articles</Link> |
                        <Link to={ROOT + "article/" + extractIdFromUrl()}>{article.meta.title}</Link>
                    </div>
                    {article.sections.map((part , idx) => {
                        return (
                            <section id={"section-" + idx}>
                                <h3>{part.title}</h3>
                                <p>{part.content}</p>
                            </section>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}