import Topbar from "../../components/topbar"
import Finisher from "../../components/finisher"
import videos from "../../db/videos";
import './style.css';

function extractIdFromUrl() {
    const parts = window.location.pathname.split('/');
    const id = parts[3];
  
    return id;
}
  
export default (props) => {
    let { videoId } = props;
    
    if ( !videoId ) {
        videoId = extractIdFromUrl();
    }

    const video = videos[videoId];

    

    return (
        <div className="theatre">
            <Finisher skew={0} style={{ height: '70px' }}></Finisher>
            <Topbar />
            <div className="video-player">
                <iframe autoplay={true} src={"https://www.youtube.com/embed/" + video.config.vendorId + '?autoplay=1'} allowFullScreen={true} frameBorder={0} style={{ width: '100%' , height: 'calc(100% + 120px)' , top: '-80px' , position: 'relative' }}></iframe>
            </div>
            <div className="video-desc">
                <h2>{video.meta.title}</h2>
                <p>{video.meta.description}</p>
            </div>
        </div>
    )
}